body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI',
		'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas,
		'Courier New', monospace;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.container{
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
    min-height: 100vh;
}

.dark-mode{
    background-color: black;
}

.dark-mode h1{
    color: white;
}

.note{
    background-color: rgb(200, 250, 61);
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
}

.note-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.notes-list{
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(250px,1fr));
    /* fr is fractional unit */

}

.note.new{
    background-color: rgb(12, 251, 251);    
}
textarea{
    border:none;
    resize: none;
    background-color:rgb(12, 251, 251);

}

textarea:focus{
    outline: none;
}

.save{
    background-color: rgb(205, 241, 243);
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
}

.save:hover{
    background-color: rgb(255, 255, 255);
    cursor: pointer;
}

.delete-icon{
    cursor: pointer;
}

.search{
    display: flex;
    align-items: center;
    background-color: rgb(199, 210, 224);
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 1.5em;
    margin-top: 1.05em;
}

.search input{
    border: none;
    background-color: rgb(199, 210, 224);
}

.search input:focus{
    outline:none;
}
